import React from 'react';
import {Route, Switch} from "react-router-dom";
import SignInPage from "../pages/SignInPage";
import SignUpPage from "../pages/SignUpPage";
import SellPage from "../pages/SellPage";
import ItemPage from "../pages/ItemPage";
import ItemEditPage from "../pages/ItemEditPage";
import ItemBuyPage from "../pages/ItemBuyPage";
import TransactionPage from "../pages/TransactionPage";
import UserPage from "../pages/UserPage";
import UserSettingPage from "../pages/UserSettingPage";
import BuyCompletePage from "../pages/BuyComplete";
import ItemListPage from "../pages/ItemListPage";

interface route {
    [name: string]: string
}

export const routes: route = {
    top: '/',
    login: '/login',
    register: '/register',
    sell: '/sell',
    item: '/items/:item_id',
    itemEdit: '/items/:item_id/edit',
    buy: '/items/:item_id/buy',
    buyComplete: '/buy/complete',
    transaction: '/transactions/:transaction_id',
    user: '/users/:user_id',
    userSetting: '/users/setting',
};

export const AppRoute: React.FC = () => {
    return (
        <Switch>
            <Route exact path={routes.top}         component={ItemListPage} />
            <Route exact path={routes.login}       component={SignInPage} />
            <Route exact path={routes.register}    component={SignUpPage}/>
            <Route exact path={routes.sell}        component={SellPage} />
            <Route exact path={routes.item}        component={ItemPage} />
            <Route exact path={routes.itemEdit}    component={ItemEditPage} />
            <Route exact path={routes.buy}         component={ItemBuyPage} />
            <Route exact path={routes.buyComplete} component={BuyCompletePage} />
            <Route exact path={routes.transaction} component={TransactionPage} />
            <Route exact path={routes.user}        component={UserPage} />
            <Route exact path={routes.userSetting} component={UserSettingPage} />
        </Switch>
    );
};
